.snackbar, .main, .mobileToggler {
  display : flex;
  flex-direction: column;
}

.main {
  height: 100%;
  width: 100%;
}

.topArea {
  width: 100%;
  height : auto;
  background : rgba(41, 240, 26, 0.6);
}

.mobileToggler {
  justify-content: center;
  align-items: center;
  width : 40px;
  height : 40px;
  //border: 1px solid black;
  border-radius: 5px;
  //background-color: white;
  margin : 5px;
}

.mobileToggler img {
  object-fit: contain;
  width: 80%;
  height : 80%;
}

.navbar {
  margin-top: 0px;
}

@media only screen and (min-width : 800px) {

  .mobileToggler {
    display : none;
  }

  .sideNavToggler, .sideNavArrow {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .sideNavToggler {
    position: absolute;
    z-index: 4;
    width: 50px;
    height: 100%;
    right: 98%;
  }

  .sideNavToggler:hover {
    background: rgba(0,0,0,0.7);
  }

  .sideNavArrow {
    width: 50%;
    height: 5%;
    border-radius: 0px 50px 50px 0px;
    background: white;
  }

  .arrow {
    height: 50%;
    width: 50%;
  }
}
